:host {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

main {
  padding-top: 5em;
  margin-bottom: auto;
}

h1 {
  font-size: clamp(34px, 4vw, 60px);
}

p {
  font-size: clamp(20px, 2vw, 32px);
}

.spacingDiv {
  padding-right: 30vw;
  min-height: 100vh;
}

section {
  align-items: center;
}
